<template>
  <div class="infinite-messages-app-header">
    <div>Messages</div>
    <input class="message-filter-input-field" type="text" placeholder="Filter by name" v-model="filterName">
    <img v-if="!filterName" @drop="onDrop($event)" @dragover.prevent @dragenter.prevent class="hide-message-icon"
      src="../assets/icons/hide-icon.svg" height="20px" alt="hide" />
  </div>
</template>

<script>
import { EventBus } from '@/event-bus';
/**
 * Header component with heading as 'Messages'
 */
export default {
  name: "AppHeader",
  data() {
    return {
      filterName: ""
    }
  },
  watch: {
    filterName() {
      EventBus.$emit('on-filter-name', this.filterName);
    }
  },
  methods: {
    /**
     * @description handling drop event - partially implemented
     * @param {any} event
     */
    onDrop(event) {
      EventBus.$emit('on-remove-message', event.dataTransfer.getData("itemIndex"));
      event.preventDefault();
    },
  },

};
</script>

<style lang="scss">
@import "../assets/tokens";

.infinite-messages-app-header {
  height: 60px;
  line-height: 60px;
  background-color: $primary-color;
  color: $white;
  text-align: left;
  padding-left: 10%;
  font-size: $heading-size;
  display: flex;

  .hide-message-icon {
    height: 40px;
    margin: 10px;
    margin-left: auto;
  }

  .message-filter-input-field {
    border: none;
    border-radius: 4px;
    margin: 12px 0;
    outline: none;
    padding: 10px;
    box-sizing: border-box;
    transition: .3s;
    font-size: 16px;
    margin-left: 10%;
  }

  @media only screen and (max-width: 1000px) {
    .message-filter-input-field {
      display: none;
    }
  }
}
</style>
