<template>
  <div class="message-card-author-name">
    <span class="author-name">{{ author }}</span>
    <span class="author-name-updated">{{ updated }}</span>
  </div>
</template>

<script>
/**
 * Component to display author name and updated time in designed format
 */
import { formatTime } from "@/utils/helper";
export default {
  props: ["message"],
  name: "AuthorName",
  data() {
    return {
      timeToDisplay: "",
      formatTime: formatTime,
    };
  },
  computed: {
    author() {
      return this.message.author.name;
    },
    updated() {
      return this.formatTime(this.message.updated);
    }
  }
};
</script>

<style lang="scss">
@import "../assets/tokens";

.message-card-author-name {
  text-align: left;
  margin-left: 20px;
  display: inline-block;

  .author-name {
    font-size: $title-size;
    font-weight: bold;
    line-height: 26px;
    color: $primary-font;
  }

  .author-name-updated {
    font-size: $paragraph-size;
    display: block;
    color: $secondary-font;
    line-height: 18px;
  }
}
</style>
