<template>
  <div class="message-panel-message-card" draggable @dragstart="startDrag($event, index)" @dragend="endDrag($event)">
    <div class="message-card-top-block">
      <user-avatar :userUrl="userURL"></user-avatar>
      <author-name :message="source"></author-name>
    </div>
    <span class="message-content">{{ source.content }}</span>
  </div>
</template>
<script>
import UserAvatar from "@/components/UserAvatar";
import AuthorName from "@/components/AuthorName";
export default {
  props: ["index", "source"],
  name: "MessageCard",
  computed: {
    userURL() {
      return this.source.author.photoUrl
    }
  },
  components: {
    UserAvatar,
    AuthorName,
  },
  methods: {
    startDrag(event, index) {
      event.dataTransfer.dropEffect = "move";
      event.dataTransfer.effectAllowed = "move";
      event.dataTransfer.setData("itemIndex", index);
      event.srcElement.style.opacity = '0.5';
    },
    endDrag(event) {
      event.srcElement.style.opacity = '1';
    }
  },
};
</script>

<style scoped lang="scss">
@import "../assets/tokens";

.message-panel-message-card {
  padding: 20px;
  background-color: $card-background;
  box-shadow: 0px 3px 4px 0px $secondary-font;
  cursor: grab;
  max-width: 1200px;
  margin: 10px 20px;

  .message-card-top-block {
    display: inline-flex;
    margin-bottom: 15px;
  }

  .message-content {
    color: $primary-font;
    -webkit-box-orient: vertical;
    display: -webkit-box;
    -webkit-line-clamp: 4;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: normal;
    line-height: 22px;
    font-size: $paragraph-size;
  }
}

.message-panel-message-card:hover {
  box-shadow: 1px 5px 6px 0px $secondary-font;
}

@media only screen and (min-width: 1200px) {
  .message-panel-message-card {
    margin: 10px auto;
  }
}
</style>