<template>
  <img class="message-card-user-avatar" :src="imageSource" alt="user" />
</template>

<script>
import { messageURL } from "@/utils/helper";
export default {
  props: ["userUrl"],
  name: "UserAvatar",
  data() {
    return {
      imageSource: "",
      messageURL: messageURL,
    };
  },
  mounted() {
    this.imageSource = messageURL + this.userUrl;
  },
};
</script>

<style scoped lang="scss">
.message-card-user-avatar {
  height: 50px;
  border-radius: 25px;
  display: inline-block;
  transition: 1s;
  cursor: zoom-in;
}
.message-card-user-avatar:hover {
  height: 100px;
  border-radius: 5px;
  transition: 1s;
}
</style>
