<template>
  <div>
    <virtual-list id="infiniteMessagePanel" :data-key="'id'" :data-sources="filteredMessages" :data-component="MessageCard" />
    <button @click="scrollToTop" v-if="showScrollToTop" type="button" class="scroll-to-top-button"
      title="Scroll to top">^</button>
  </div>
</template>

<script>
/**
 * The parent panel for messages blocks. Using virtual list here to render messages. Virtual list helps us to load large amount of data without affecting performance
 */
import MessageCard from "@/components/MessageCard";
import { messageURL } from "@/utils/helper";
import VirtualList from "vue-virtual-scroll-list";
import { EventBus } from '@/event-bus';
export default {
  name: "InfiniteMessagePanel",
  data() {
    return {
      showScrollToTop: false,
      messages: [],
      bufferMessages: [],
      pageToken: "",
      loading: false,
      filterName: "",
      requestParams: {
        limit: 50,
      },
      messageURL: messageURL,
      MessageCard: MessageCard,
    };
  },
  computed: {
    filteredMessages() {
      if (this.filterName)
        return this.messages.filter((message) => {
          return message.author.name.toLowerCase().indexOf(this.filterName.toLowerCase()) >= 0;
        })
      return this.messages;
    }
  },
  components: {
    VirtualList,
  },
  methods: {
    onFilterName(value) {
      this.filterName = value;
    },
    scrollToTop() {
      const panel = document.getElementById("infiniteMessagePanel");
      panel.scrollTop = 0;
    },
    /**
     * @description by using scroll height, scroll top and offset height of the current panel, deciding to get the next set of messages
     * @returns {any}
     */
    canFetchMessages() {
      const panel = document.getElementById("infiniteMessagePanel");
      panel.scrollTop > 800 && panel.clientWidth > 800 ? this.showScrollToTop = true : this.showScrollToTop = false;
      if (panel.offsetHeight === 0) {
        return true;
      }
      if (panel.scrollTop >= panel.scrollHeight - panel.offsetHeight - 200) {
        return true;
      } else {
        return false;
      }
    },

    /**
     * @description This function will be rendered on scroll of messages
     */
    onScroll() {
      if (this.loading) return;
      if (this.canFetchMessages()) {
        this.getMessages();
      }
    },

    /**
     * @description Using set of buffered messages for smooth scroll
     */
    getMessages() {
      if (this.bufferMessages && this.bufferMessages.length) {
        this.messages = [...this.messages, ...this.bufferMessages];
      }
      this.loadBufferMessages();
    },

    async loadBufferMessages() {
      const url =
        this.messageURL +
        "/messages?" +
        new URLSearchParams(this.requestParams).toString();
      this.loading = true;
      try {
        const response = await fetch(url);
        this.loading = false;
        if (response.ok) {
          const data = await response.json();
          this.requestParams.pageToken = data.pageToken;
          this.bufferMessages = [...data.messages];
          if (!this.messages.length) {
            this.getMessages();
          }
        } else throw new Error(response.error);
      } catch (error) {
        //error to be displayed here
        this.loading = false;
        return;
      }
    },

    onRemoveMessage(index) {
      this.messages.splice(index, 1)
    }
  },

  mounted() {
    EventBus.$on('on-remove-message', this.onRemoveMessage);
    EventBus.$on('on-filter-name', this.onFilterName);
    this.getMessages();
    document
      .getElementById("infiniteMessagePanel")
      .addEventListener("scroll", this.onScroll);
  },

  beforeDestroy() {
    EventBus.$off('on-remove-message', this.onRemoveMessage);
    EventBus.$off('on-filter-name', this.onFilterName);
    document
      .getElementById("infiniteMessagePanel")
      .removeEventListener("scroll", this.onScroll);
  },
};
</script>

<style lang="scss">
@import "../assets/tokens";

#infiniteMessagePanel {
  height: calc(100vh - 60px);
  overflow-y: auto;
  background-color: $app-background;
  scroll-behavior: smooth;
}

.scroll-to-top-button {
  position: fixed;
  right: 20px;
  bottom: 30px;
  cursor: pointer;
  background-color: $primary-color;
  color: $white;
  border: none;
  font-size: 30px;
  border-radius: 30px;
  height: 30px;
  width: 30px;
  box-shadow: 1px 5px 6px 0px $secondary-font;
}

.scroll-to-top-button:hover {
  height: 31px;
  width: 31px;
  transform: 1s;
}
</style>
