const messageURL = 'http://message-list.appspot.com';

function formatTime(time) {
  const timeDifference = Math.abs(new Date().getTime() - new Date(time).getTime());
  const timeInSeconds = Math.round(timeDifference / 1000);
  const timeInMinutes = Math.round(timeInSeconds / 60);
  const timeInHours = Math.round(timeInMinutes / 60);
  const timeInDays = Math.round(timeInHours / 24);
  const timeInWeeks = Math.round(timeInDays / 7);
  const timeInYears = Math.round(timeInWeeks / 52);

  if (timeInYears > 0) {
    return timeInYears + ' year' + (timeInYears > 1 ? 's ago' : ' ago');
  } else if (timeInWeeks > 0) {
    return timeInWeeks + ' week' + (timeInWeeks > 1 ? 's ago' : ' ago');
  } else if (timeInDays > 0) {
    return timeInDays + ' day' + (timeInDays > 1 ? 's ago' : ' ago');
  } else if (timeInHours > 0) {
    return timeInHours + ' hour' + (timeInHours > 1 ? 's ago' : ' ago');
  } else if (timeInMinutes > 0) {
    return timeInMinutes + ' minute' + (timeInMinutes > 1 ? 's ago' : ' ago');
  } else {
    return timeInSeconds + ' second' + (timeInSeconds > 1 ? 's ago' : ' ago');
  }
}

export { messageURL, formatTime }
